export function weixinShare(Vue,data){
	var url="http://dev.kofuf.com/api/session/share_weixin_config?url="+encodeURIComponent(window.location.href.split('#')[0]);
	Vue.http.get(url)
	.then((res)=>{return res.bodyText;})
	.then((data)=>{
		var data=JSON.parse(data);
		wx.config({
			debug: false, // ,apialertpc
			//logpc
			appId: data.appId, // 
			timestamp: data.timestamp, // 
			nonceStr: data.nonceStr, // 
			signature: data.signature,// 1
			jsApiList: [
				'checkJsApi',
				'onMenuShareTimeline',
				'onMenuShareAppMessage',
				'onMenuShareQQ',
				'onMenuShareWeibo'
			] // JSJS2
		});
	},(err)=>{
		error(err)
	});
	wx.ready(function(){
    // 
    var title=shareData.title;
    var link=shareData.link;
    var imgUrl=shareData.imgUrl;
    var desc=shareData.desc;
    wx.onMenuShareTimeline({
      title: title, // 
      link: link, // JS
      imgUrl:imgUrl , // 
      success: function () {
        // alert("")
          // 
      },
      cancel: function () {
        // alert("")
          // 
      }
    });
    // 
    wx.onMenuShareAppMessage({
      title: title, // 
      desc:desc, // 
      link: link, // JS
      imgUrl: imgUrl, // 
      type: 'link', // ,musicvideolinklink
      // dataUrl: '', // typemusicvideo
      success: function () {
        // alert("")
          // 
      },
      cancel: function () {
        // alert("")
          // 
      }
    });
    //QQ
    wx.onMenuShareQZone({
      title: title, // 
      desc: desc, // 
      link: link, // 
      imgUrl: imgUrl, // 
      success: function () {
        // 
      },
      cancel: function () {
        // 
      }
    });

    //QQ
    wx.onMenuShareQQ({
      title: title, // 
      desc: desc, // 
      link: link, // 
      imgUrl: imgUrl, // 
      success: function () {
         // 
      },
      cancel: function () {
         // 
      }
    });
	});
}



